<template>
    <header id="header">
        <h2>智慧工地管理系统</h2>
        <div class="input">
            <input type="text" placeholder="选择工地" class="choice-project"/>
            <img src="../assets/images/index/search.png" alt="" class="search"/>
        </div>
        <div class="video">
            现场录像
        </div>
        <p class="safe-day">安全施工<span class="day">345</span>天</p>
    </header>

</template>
<script>
    export default {
        name:'Index'
    }
</script>
<style>
#header{
    border-top:1px solid #000;
    height:100%;
}
h2{
    font-size: 17px;
    color: #FFFFFF;
    margin:15px auto;

}
    .search{
        width: 13px;
        height:13px;
        position: absolute;
        left:35%;
        top:10px;

    }
    .input{
        width: 100%;
        padding:0 3%;
        height:28px;
        text-align: center;
        margin:9px auto;
        position: relative;
    }
.choice-project{
    width: 100%;
    height:100%;
    margin:0 auto;
    background: #212121;
    border-radius: 6px;
    text-align: center;
    border:none;
    outline:none;
    font-size: 14px;
    color: #FFF;
}
    .video{
        width: 95%;
        height:38%;
        border: 1px solid #FFF;
        margin:2% auto;
        color:#FFF;
    }
    .day{
        font-size: 36px;
        color: #FFD500;
        text-align: center;
    }
    .safe-day{
        font-size: 17px;
        color: #FFFFFF;
    }

</style>


